    margin: 0 auto; /* Centra la caja de texto */
  }
  
  .online-parts {
    display: block; /* Asegura que el botón se comporte como un bloque */
    background-color: #1C5BDA !important; /* Fondo azul */
    color: white !important; /* Letra blanca */
