  .modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    display: flex;
    justify-content: flex-end; /* Alinear contenido a la derecha */
    align-items: center;
    z-index: 10000;
  }
  
  .modal-content {
    background-color: #fff;
    position: relative;
    width: 46%; /* Ajusta el ancho para hacer la ventana más pequeña */
    height: 96%; /* Ajusta el alto para hacer la ventana más pequeña */
    padding: 20px;

  }
  
  .image-text-pair {
    display: flex;
    align-items: flex-start;
    flex-direction: row; /* Invierte el orden de los elementos */
  }
  
  .modal-content img {
    max-width:40%  !important; /* Ajusta el ancho a tu preferencia */
    max-height: 50%; /* Ajusta el alto a tu preferencia */
    margin-left: 25rem;
    margin-right: 0 !important;
    margin-bottom: 1rem;
    margin-top: -6rem;
    
  }
  .modal-title{
    font-weight: bold;
    font-size: 20px;
    margin-bottom: 1rem;
  }
  .modal-subtitle{
    font-weight: bold;
    width: 50%;
    font-size: 18px;
    margin-top: 3rem;

  }
  .modal-text{
    font-size: 16px;
    max-width: 50%;
    margin-top: 0rem;
    margin-bottom: -3rem;
  }
  .serial-devider{
    margin: 0!important;
    margin-bottom: 0rem !important;
  }
  .modal-content .description {
    flex: 1;
    font-size: 14px; /* Ajusta el tamaño de la fuente según tu preferencia */
    margin: 0;
  }
  
  .close-btn {
    position: absolute;
    font-size: 20px;
    top: 10px;
    right: 10px;
    width: 2.5rem;
    height: 2.5rem;
    cursor: pointer;
    border-radius: 100%;
  }