body, .header, .footer {
    font-family: 'Do Hyeon', sans-serif !important;
  }
  
  .logo{
      margin-right: 50px;
      width: 200px;
      height: 120px;
      display: block;
  }

  
  .header-Grid2-us{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem !important;
    padding-left: 0rem !important;
    padding-right: 0rem  !important; 
    border-bottom: 0rem solid #e9ecef;
    background-color: #ffffff;
    position: fixed;
    z-index: 100 ;
    top : 0.01rem;
    right: 0rem !important;
    }
  
  .header-about-us .MuiButton-root {
    margin: 0 40px; /* Ajusta el margen entre los botones */
    font-weight: bold !important; /* Añade negrita al texto de los botones */
    font-family: inherit !important; /* Utiliza la fuente por defecto del navegador */
    font-size: 15px !important; /* Ajusta el tamaño de la fuente */
    color: #000000;
    text-decoration: none !important; /* Elimina el subrayado de los enlaces */ 
  }


  .slider{
    margin-top: 10px;
    }
    .slick-slide img {
      margin-top: 125px !important;
      width: 100%;
      height: 600px !important;
      object-fit: cover;
      margin-bottom: -27px !important;
    }
    
    .slick-dots li button:before {
      font-family: 'slick';
      font-size: 85px !important;
      line-height: 0px !important;
      position: absolute;
      left: 0;
      width: 20px;
      height: 20px;
      content: '-' !important;
      text-align: center;
      opacity: 0.25;
      color: #DA291C!important;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
    }
    
    .slick-dots{
      bottom: 40px !important;
    }


    .about-us-image{
     padding-top: 20px !important;   
    }


    .about-us-content {
        margin-left: 20%; /* Margen general */;
        margin-top: 3%;
        margin-bottom: 5%;
        max-width: 50%; /* Ancho máximo */
        padding: 25px; /* Agrega padding para organizar el contenido */
        padding-left: 50px;
        padding-right: 50px;
        background-color: #e9ecef;
        border-radius: 20px; /* Borde redondeado */
        border-style: outset;
        text-transform: uppercase;
        letter-spacing: 1px; /* Espacio entre letras */
        line-height: 1.5;
      }
      .about-us-title{
        font-weight: bold; /* Texto en negrita */
        font-size: 40px;
        color: #DA291C;
        text-align: center;
        padding-top: 40px;
      }
      
      .about-us-text {
        font-weight: bold; /* Texto en negrita */
        font-size: 20px; /* Tamaño de fuente */
      }
      
      .about-us-list {
        padding-left: 20px; /* Padding para la lista */
        list-style-type: disc; /* Estilo de lista con viñetas */
      }
      
      .about-us-list li {
        margin-bottom: 10px; /* Espacio entre los elementos de la lista */
      }

      .map-container{
        height: 80%;
        width: 80%;
        margin-top: 14%;
        padding: 2%;
        padding-bottom: 4%;
        background-color: #DA291C;
        border-radius: 10px;
  
      }