body, .header, .footer {
    font-family: 'Do Hyeon', sans-serif !important;
  }
  
  .logo{
      margin-right: 50px;
      width: 200px;
      height: 120px;
      display: block;
  }

  
  .header-Grid2{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem !important;
    padding-left: 0rem !important;
    padding-right: 0rem  !important; 
    border-bottom: 0rem solid #e9ecef;
    background-color: #ffffff;
    position: fixed;
    z-index: 100 ;
    top : 0.01rem;
    right: 0rem !important;
  }
  
  .header-Service .MuiButton-root {
    margin: 0 40px; /* Ajusta el margen entre los botones */
  font-weight: bold !important; /* Añade negrita al texto de los botones */
  font-family: inherit !important; /* Utiliza la fuente por defecto del navegador */
  font-size: 15px !important; /* Ajusta el tamaño de la fuente */
  color: #000000;
  text-decoration: none !important; /* Elimina el subrayado de los enlaces */ 
  }

  .contact-button {
    background-color: #1C5BDA !important;
    color: white !important;
    border-radius: 30px !important;
    padding: 12px 25px !important;
}

.video-container-motores { 
    margin-top: 180px!important;
    display: flex; 
    justify-content: center; 
    align-items: center; 
    margin: 20px; 
} 
  
.custom-video-motores { 
    width: 100%;
    max-width: 1000px; 
    max-height: 700px;
    border: 2px solid #000; 
    border-radius: 10px; 
}

.services-container{
    justify-content: center;
    align-items: center;
    margin-top: 100px;
    background-color: black;
    max-width: 70%;
    margin-left: 11%;
    margin-bottom: 5%;
    padding: 10px;
    padding-left: 5%;
    padding-right: 5%;
    border-radius: 20px;
}

.service-title-text{
    font-weight: bold;
    font-size: 30px;
    color: #ffffff;
    text-align: center;
    padding-top: 20px;
}
.service-text{
    font-size: 20px;
    color: #ffffff;
    text-align: center;
    padding-top: 20px;
    padding-bottom: 20px;
}

.div-bot{
    margin-bottom: 20px !important;

}


.services-Grid2 {
    display: flex;
    flex-wrap: wrap;
    justify-content: center; /* Centra las cards horizontalmente */
  }
.service-card {
    background-color: #f0f0f0; /* Color gris */
    padding: 20px;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 310px; /* Altura fija para todas las cards */
    width: 18%; /* Ancho fijo para todas las cards */
    margin: 50px; /* Aumenta la distancia entre cada card */
    margin-top: 0px;
    cursor: pointer;
  }
  
  .service-card-content {
    flex-grow: 1;
  }
  
  .service-card-title {
    color: #DA291C; /* Títulos en rojo */
    font-size: 20px; /* Tamaño de fuente */
    margin-bottom: 10px; /* Espacio debajo del título */
    font-weight: bold;
    text-align: center;
  }
  
  .service-card-text {
    text-align: left; /* Texto alineado a la izquierda */
    color: black; /* Color del texto */
    flex-grow: 1; /* Permite que el texto ocupe el espacio disponible */
    font-weight: bold;
    justify-content: center;
    font-size: 14px;
    padding-left: 10px;
  }
  .serv-title{
    color: #DA291C;
    font-size: 30px;
    font-weight: bold;
    text-align: center;
    padding-bottom: 20px;
  }
  .service-card-content svg {
    margin-left: 36%; /* Añade espacio entre el logo y el texto */
  }

  .form-section {
    background-color: #f0f0f0; /* Fondo gris */
    padding: 20px;
    border-radius: 8px;
    display: flex;
    justify-content: space-between; /* Acomoda los Grid2s con espacio entre sí */
    align-items: flex-start; /* Alinea los Grid2s al inicio verticalmente */
    max-width: 55%; /* Ajusta el ancho del contenedor */
    margin: 0 auto; /* Centra el contenedor horizontalmente */
    margin-bottom: 10px;
    margin-top: 20px;
  }

  .form-title-serv{
    color:#1C5BDA; /* Títulos en rojo */
    font-size: 30px; /* Tamaño de letra */
    font-weight: bold; /* Letra en negrita */
    margin-top: 80px !important; /* Ajusta el margen inferior */
    text-align: center;
  }
  
  .contact-info-title-serv{
    color: red; /* Títulos en rojo */
    font-size: 22px; /* Tamaño de letra */
    font-weight: bold; /* Letra en negrita */
    margin-bottom: 0px !important; /* Ajusta el margen inferior */
}
  
  .tittles-serv {
    color: black; /* Labels de los inputs en negro */
    font-weight: bold; /* Labels en negrita */
    font-size: 17px;
    margin-bottom: 2px !important;
    margin-top: 2px !important;
  }
  
  .form-group-serv input[type="text"] {
    width: 300px;
    padding: 10px;
    margin-top: 5px;
    margin-bottom: 15px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .form-group-serv input[type="text"][placeholder="SOME OBSERVATIONS"] {
    height: 100px; /* Más grueso que el resto */
    resize: none; /* Evita el redimensionamiento */
    overflow-wrap: break-word; /* Salto de línea al llegar al borde */
    padding: 10px; /* Añade padding para que el texto no toque el borde */
    box-sizing: border-box; /* Incluye el padding y el borde en el tamaño total del input */
    vertical-align: top; /* Alinea el texto en la parte superior */
  }
  
  .form-division-serv {
    width: 300px !important; /* Ajusta el ancho de las divisiones */
    margin: 10px 0; /* Añade margen vertical */
  }
  
  .Grid2-item-service{
    margin: 0 30px; /* Ajusta el margen entre los Grid2s */
  }
  
  .send-button-serv {
    background-color: #1C5BDA; /* Fondo azul */
    font-weight: bold;
    color: white; /* Letra blanca */
    padding: 10px 70px; /* Tamaño del botón */
    border: none;
    border-radius: 18px;
    cursor: pointer;
    font-size: 16px; /* Tamaño de la letra */
    text-align: center;
    display: inline-block;
    margin: 45px 50px;
  }
  
  .send-button-serv:hover {
    background-color: rgb(40, 43, 211); /* Fondo azul oscuro al pasar el cursor */
  }

  .slider{
    margin-top: 10px;
    }
    .slick-slide img {
      margin-top: 132px !important;
      width: 100%;
      height: 600px !important;
      object-fit: cover;
      margin-bottom: -27px !important;
    }
    
    .slick-dots li button:before {
      font-family: 'slick';
      font-size: 85px !important;
      line-height: 0px !important;
      position: absolute;
      left: 0;
      width: 20px;
      height: 20px;
      content: '-' !important;
      text-align: center;
      opacity: 0.25;
      color: #DA291C!important;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
    }
    
    .slick-dots{
      bottom: 40px !important;
    }

    .map-container{
      height: 80%;
      width: 80%;
      margin-top: 14%;
      padding: 2%;
      padding-bottom: 4%;
      background-color: #DA291C;
      border-radius: 10px;
      
    }


